/**
 * SlateDevTools theme for JavaScript, CSS and HTML
 * Loosely based on okaidia theme by http://www.monokai.nl/
 * @author zarv1k
 */
.slate-dev-tools {
  /* Code blocks */
  /* Inline code */
}
.slate-dev-tools code[class*=language-],
.slate-dev-tools pre[class*=language-] {
  color: #e0e0e0;
  background: none;
  /*text-shadow: 0 1px rgba(0, 0, 0, 1);*/
  font-family: monaco, Consolas, "Lucida Console", monospace;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}
.slate-dev-tools pre[class*=language-] {
  padding: 1em;
  margin: 0.5em 0;
  overflow: auto;
  border-radius: 0.3em;
}
.slate-dev-tools :not(pre) > code[class*=language-],
.slate-dev-tools pre[class*=language-] {
  background: #2a2f3a;
}
.slate-dev-tools :not(pre) > code[class*=language-] {
  padding: 0.1em;
  border-radius: 0.3em;
  white-space: normal;
}
.slate-dev-tools .token.comment,
.slate-dev-tools .token.prolog,
.slate-dev-tools .token.doctype,
.slate-dev-tools .token.cdata {
  color: #969896;
}
.slate-dev-tools .token.punctuation {
  color: #f8f8f2;
}
.slate-dev-tools .namespace {
  opacity: 0.7;
}
.slate-dev-tools .token.property,
.slate-dev-tools .token.tag,
.slate-dev-tools .token.constant,
.slate-dev-tools .token.symbol,
.slate-dev-tools .token.deleted {
  color: #6fb3d2;
}
.slate-dev-tools .token.boolean,
.slate-dev-tools .token.number {
  color: #b294bb;
}
.slate-dev-tools .token.selector,
.slate-dev-tools .token.attr-name,
.slate-dev-tools .token.string,
.slate-dev-tools .token.char,
.slate-dev-tools .token.builtin,
.slate-dev-tools .token.inserted {
  color: #a1c668;
}
.slate-dev-tools .token.operator,
.slate-dev-tools .token.entity,
.slate-dev-tools .token.url,
.slate-dev-tools .language-css .token.string,
.slate-dev-tools .style .token.string,
.slate-dev-tools .token.variable {
  color: #f8f8f2;
}
.slate-dev-tools .token.atrule,
.slate-dev-tools .token.attr-value,
.slate-dev-tools .token.function,
.slate-dev-tools .token.class-name {
  color: #f0c674;
}
.slate-dev-tools .token.keyword {
  color: #66d9ef;
}
.slate-dev-tools .token.regex,
.slate-dev-tools .token.important {
  color: #fc6d24;
}
.slate-dev-tools .token.important,
.slate-dev-tools .token.bold {
  font-weight: bold;
}
.slate-dev-tools .token.italic {
  font-style: italic;
}
.slate-dev-tools .token.entity {
  cursor: help;
}.slate-debugger-id {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 10px;
  font-weight: normal;
  cursor: pointer;
}.slate-dev-tools {
  position: fixed;
  bottom: 0;
  right: 0;
  top: 0;
  width: 380px;
  font-family: monaco, Consolas, "Lucida Console", monospace;
  z-index: 10000;
  box-shadow: 0 0 6px 1px rgba(102, 102, 102, 0.6);
  display: flex;
  flex-direction: column;
}
.slate-dev-tools.debug-json-tree .debug-body {
  height: 100%;
}
.slate-dev-tools.debug-json-tree .debug-body > ul {
  padding-left: 5px !important;
  margin: 0 !important;
  width: 100%;
  overflow: auto;
}
.slate-dev-tools.collapsed {
  width: 0;
  box-shadow: none;
}
.slate-dev-tools.collapsed .debug-actions {
  visibility: hidden;
}
.slate-dev-tools.collapsed .hide-toggle {
  display: block;
}
.slate-dev-tools .debug-body {
  display: flex;
  height: 100%;
  font-size: 12px;
}
.slate-dev-tools .debug-body .raw-content {
  font-family: monaco, Consolas, "Lucida Console", monospace;
  position: relative;
  width: 100%;
  padding: 5px;
  margin: 0;
  border: none;
  border-radius: 0;
  z-index: 4;
  font-size: 12px;
}
.slate-dev-tools .debug-head {
  flex: 0 0 auto;
  background-color: #fbfbfb;
  z-index: 5;
  box-shadow: 1px 1px 2px 0 rgba(102, 102, 102, 0.6);
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  height: 22px;
}
.slate-dev-tools .debug-head .debug-title {
  padding-left: 10px;
  color: #414141;
  font-size: 12px;
  white-space: nowrap;
}
.slate-dev-tools .debug-head .debug-title .slate-dev-tools-id {
  font-weight: bold;
}
.slate-dev-tools .debug-head .debug-title .slate-dev-tools-icon {
  width: 22px;
  height: 22px;
  display: inline-block;
  vertical-align: top;
}
.slate-dev-tools .debug-head .debug-title .slate-dev-tools-icon img {
  opacity: 0.7;
}
.slate-dev-tools .debug-head .debug-title .debug-title-mode {
  line-height: 22px;
  vertical-align: top;
}
.slate-dev-tools .debug-head .debug-actions {
  flex: 0 0 auto;
}
.slate-dev-tools .debug-head .debug-actions button {
  cursor: pointer;
  padding: 0 2px;
  border: none;
  opacity: 0.5;
  background: none;
}
.slate-dev-tools .debug-head .debug-actions button:hover {
  opacity: 1;
}
.slate-dev-tools .debug-head .debug-actions button.toggle-sync.hide {
  display: none;
}
.slate-dev-tools .debug-head .debug-actions button.toggle-sync.active {
  opacity: 1;
}
.slate-dev-tools .debug-head .debug-actions button > span {
  display: block;
  width: 22px;
  height: 22px;
}
.slate-dev-tools .hide-toggle {
  line-height: 0;
  display: none;
  padding: 0;
  position: absolute;
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
  border-width: 0;
  padding: 4px;
  background-color: #2a2f3a;
  color: #fff;
  top: 50%;
  left: -17px;
  z-index: 3;
  cursor: pointer;
  transition: left 0.2s;
  margin-right: -27px;
}
.slate-dev-tools .hide-toggle svg {
  width: 14px;
  height: 14px;
  fill: #fff;
}
.slate-dev-tools .hide-toggle svg:first-child {
  padding-right: 5px;
}
.slate-dev-tools .hide-toggle:hover {
  left: -41px;
}
.slate-dev-tools .hide-toggle:hover svg:first-child {
  width: 14px;
}.slate-dev-tools .slate-dev-tools-icon {
  color: #414141;
}
.slate-dev-tools .slate-dev-tools-icon.icon-iCursor img {
  max-height: 16px;
  padding: 3px;
}
.slate-dev-tools .slate-dev-tools-icon.icon-curly img {
  max-height: 20px;
  padding: 1px;
}